#heart {
  position: relative;
  width: 300px;
  height: 270px;
  animation: heartbeat 1s infinite;
  /* margin: 0 auto; */
}
#heart:before,
#heart:after {
  position: absolute;
  content: "";
  left: 150px;
  top: 0;
  width: 150px;
  height: 240px;
  background: lightgray;
  border-radius: 150px 150px 0 0;
  transform: rotate(-45deg);
  transform-origin: 0 100%;
}
#heart:after {
  left: 0;
  transform: rotate(45deg);
  transform-origin: 100% 100%;
}

@keyframes heartbeat {
  0% {
    transform: scale(0.75);
  }
  20% {
    transform: scale(1);
  }
  40% {
    transform: scale(0.75);
  }
  60% {
    transform: scale(1);
  }
  80% {
    transform: scale(0.75);
  }
  100% {
    transform: scale(0.75);
  }
}

@keyframes init {
  0% {
    background: lightgray;
  }
  20% {
    background: lightgreen;
  }
  40% {
    background: lightgray;
  }
  60% {
    background: lightblue;
  }
  80% {
    background: lightblue;
  }
  100% {
    background: #f687b3;
  }
}

.pink:before,
.pink:after {
  animation: init 20s;
  animation-fill-mode: forwards;
}
